import React from 'react';
import classes from './BuildControls.css';
import BuildControl from './BuildControl/BuildControl';


const controls = [

  {label:'Salad',type:'salad'},
  {label:'Bacon',type:'bacon'},
  {label:'Cheese',type:'cheese'},
  {label:'Meat',type:'meat'}

];


const buildControls = (props) =>(

<div className={classes.BuildControls}>

<p>Current Price:<strong>{props.price.toFixed(2)}</strong></p>

{/* going throught the controls array of Objects */}
{controls.map(ctrl =>(

<BuildControl 
key={ctrl.label} 
label={ctrl.label}
added ={() => props.ingredientsAdded(ctrl.type)}
removed ={() =>props.ingredientRemove(ctrl.type)}
//the disabled props is the object of keys with boolean values {salad:false,cheese:true}..
disabled={props.disabled[ctrl.type]}/>

))}

<button className={classes.OrderButton}
disabled={!props.purchaseble} onClick={props.ordered}>{props.isAuth ? 'ORDER NOW': 'SIGN UP TO ORDER'}</button>

</div>
);


export default buildControls;


